import React from 'react';

class ViewRecipe extends React.Component {
  constructor(props) {
    super(props);

	this.recipeModalRef = React.createRef();
  }

  componentDidMount() {
    this.recipeModalRef.current.scrollTop = 0;
  }

  render() {
	return (
	  <div ref={this.recipeModalRef} id="view-recipe-modal" className="modal recipe">
		<div className="modal-content">
		  <div className="recipe-display" dangerouslySetInnerHTML={{__html: this.props.recipeData}}></div>
		</div>
		<div className="modal-footer" style={{backgroundColor: "white"}}>
			<a href="#!" style={{width: "25%", fontSize: "1.5em"}} className="modal-close waves-effect waves-green btn"><i className="material-icons" style={{verticalAlign: "middle", fontSize: "1.5em"}}>arrow_back</i>Exit</a>
		</div>
	  </div> 
	);
  }
}

export default ViewRecipe;
